.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.popup-inner {
  position: relative;
  padding: 32px;
  width: 100%;
  height: 100%;
  max-width: 640px;
  max-height: 500px;

  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-sizing: border-box;
  -webkit-backdrop-filter: blur(74px);
          backdrop-filter: blur(74px);
  border-radius: 16px;
}

.popup-inner h3 {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 78px;
  line-height: 98px;

